.modal-container {
    position: fixed; 
    z-index: 1; 
    left: 0;
    top: 0;
    overflow: auto; 
    background-color: white; 
    left: 50%;
    top: 10%;
    width: 640px;
    height: 65%;
    transform: translate(-50%, 0%);

    border-radius: 20px;
    padding: 50px;
    box-shadow: 5px 8px 8px rgba(0, 0, 0, 0.2);
}

.title-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 30px;
}

.modal-title {
    font-size: 45px;
    font-weight: 300;
    color: var(--primary);
}

.modal-desc {
    font-size: 14px;
    font-weight: 400;
    color: var(--primary);
}

.modal-img {
    width: 300px;
}

.modal-header {
    font-size: 25px;
    font-weight: 400;
    color: var(--primary);
    margin-top: 20px;
}

.modal-text {
    font-size: 15px;
    font-weight: 300;
    color: #503B37;
}


.back-btn {
    /* transition: all 500ms ease-in-out;  */

  
    background-color: var(--secondary);
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.25);
    border-radius: 5px;
    border: none;
    padding: 12px;
    color: white;
}

.back-btn:hover {
    cursor: pointer;
    opacity: 1;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.3);
    transition: all .2s;
}

.buttons-container {
    display: flex;
    flex-direction: row;
    margin-top:50px;
    margin-bottom: 15px;
}

.project-button {
    font-family: 'Nunito', sans-serif;
    min-width: fit-content;
    padding: 8px 10px;
    margin-right: 15px;
    color: white;
    background-color: var(--primary);
    border-radius: 13px;
    border: none;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.25);
    font-size: 12px;
}
.project-button:hover {
    opacity: 1;
    right: 0;
    box-shadow: 6px 6px 6px rgba(0, 0, 0, 0.35);
    transition: all .2s;
} 
