.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.main-page {
  display: flex;
  flex-direction: row;
  align-self: center;
}

.App-header {
  /* font-family: 'Inter', sans-serif; */
  font-family: 'Text Me One', sans-serif;
  z-index: 1;
  min-height: 85vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-left: 100px;
}

.theme-button {
  background: transparent;
  border: none;
  color: white;
  width: 20px;
  align-self: center;
  margin-top: 20px;
}

#greeting {
  font-size: 70px;
}
#intro {
  font-size: 50px;
}
#description {
  font-size: 30px;
}

.avatar {
  width: 500px;
  margin-left: -170px;
}

.date-time-container {
  align-items: flex-end;
  right: 40px;
  top: 20px;
}







.section-header {
  font-family: 'Nunito', sans-serif;
  color: white;
  font-size: 25px;
  font-weight: 300;
  margin: 50px;
  margin-left: 230px;
}

#projects {
  margin-top: 100px;
}

#artAndGallery {
  margin-top: 100px;
}

#contactMe {
  margin-top: 100px;
}



.toTop {
  max-width: fit-content;
  position: fixed;
  left: 92%;
  bottom: 60px;

  background-color: var(--secondary);
  box-shadow: -4px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 9px;
  border: none;
  padding: 15px;
  color: white;
}

.toTop:hover {
  cursor: pointer;
  position: fixed;
  opacity: 1;
  right: 0;
  box-shadow: -9px 9px 9px rgba(0, 0, 0, 0.3);
  transition: all .2s;
}

#footer {
  color: white;
  padding-top: 100px;
  padding-bottom: 60px;
  font-family: 'Nunito', sans-serif;
  align-self: center;
}